import { INLINECODE_TOGGLE } from '../actions';
import { Controller } from '@ali/4ever-cangjie';
import toggleInlineCode from '../commands/toggleInlineCode';

export default function onAction(action, controller: Controller, next) {
  const { type } = action;
  if (type === INLINECODE_TOGGLE) {
    return controller.command(toggleInlineCode);
  }
  return next();
}
